body{
  background-color: #123;
  color:#fff;
}
main{
  width: 300px;
  margin: 50px auto 0;
}
h1{
  text-align: center;
}
div.board{
  display: grid;
  border: 2px solid #fff;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
}
div.square{
  width: 100px;
  height: 100px;
  border: 2px solid #fff;
  box-sizing: border-box;
  font-size: 4rem;
  text-align: center;
}


div.result{
  font-size: 2rem;
  padding: 10px;
}
div.result-green{
  background-color: green;
  padding: 1rem;
  font-size: 1.5rem;
}
div.result-red{
  background-color: red;
  padding: 1rem;
  font-size: 1.5rem;
}

div.result-blue{
  background-color: blue;
  padding: 1rem;
  font-size: 1.5rem;
}